.user_page {
	width: calc(75% - 15px);
	background-color: #000;
	padding: 15px;

	.had {
		font-size: 24px;
		color: #fff;
		margin: 0;
	}
	.user_list {
		margin-bottom: 40px;
	}
	.top {
		align-items: center;
		justify-content: space-between;
		margin-bottom: 30px;
		input {
			background-color: transparent;
			border: 1px solid #fff;
			padding: 5px 10px;
			color: #fff;
			outline: none;
			&::-webkit-input-placeholder {
				color: #fff;
			}
		}
	}
}
