.profile__info {
	display: flex;
	flex-direction: row;
	align-items: center;
	margin-bottom: 30px;
	.avatar {
		margin-right: 30px;

		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		height: 150px;
		width: 150px;
		border-radius: 50%;
	}

	.name {
		font-size: 24px;
		color: #fff;
		margin-bottom: 5px;
	}

	.line p {
		color: #fff;
	}
}
